html, body {
  font-family: 'Source Sans Pro', Helvetica, Arial, sans-serif;
  font-size: 18px;
  line-height: 1.4;
  font-weight: 200;
  color: $gray;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: bold;
}

h1, .h1 {
  font-size: 1.6rem;
  line-height: 1.2;
  @include bp(sm) {
    font-size: 2.2rem;
  }
}

h2, .h2 {
  font-size: 1rem;
  @include bp(sm) {
    font-size: 1.6rem;
  }
}

h3, .h3 {
  font-size: 1.4rem;
  color: $brand;
}

b, strong {
  font-weight: bold;
}

i, em {
  font-style: italic;
}

ul {
  list-style-type: disc;
  padding-left: 1rem;
  margin-bottom: 2rem;
}

p {
  margin-bottom: 1rem;
}

p + ul {
  margin-top: -1rem;
}